* {
  box-sizing: border-box;
}

h1,h2,h3,h4,h5,p {
  margin: 0;
}

.page {
  scroll-behavior: smooth;
  font-family: 'Playfair Display', serif;
  font-family: 'Raleway', sans-serif;
  overflow-x: hidden;

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    background-color: $c-background;
    z-index: 1;
    width: 100%;
    height: 100%;

    opacity: 0;
    transition: all 0.3s;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}

.page:has(.page__menu:target) {
  overflow: hidden;
}
